@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;800;900&display=swap");

body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f3f7ff;
}

* {
    box-sizing: border-box;
    font-family: "Inter", sans-serif;
    outline: none;
}

.App {
    padding: 50px;
    width: 400px;
    margin: 100px auto;
    margin-bottom: 0;
    border-radius: 30px;
    background-color: #fff;
    position: relative;
    box-shadow: 0 15px 10px rgba(0, 13, 189, 0.05);
    // white-space: nowrap;
    button {
        margin-top: 30px;
        padding: 18px;
        border-radius: 15px;
        outline: none;
        border: 0;
        width: 100%;
        font-size: 16px;
        font-weight: 600;
        color: #000;
        background: #60f48d;
        background: linear-gradient(90deg, #60f48d 0%, #76f4e8 100%);
        cursor: pointer;
        transition: all 0.1s ease-in-out;
        box-shadow: 0 10px 10px #30ffba6f;

        &:hover {
            transform: scale(1.02);
        }

        &:active {
            transform: scale(0.99);
            box-shadow: 0 10px 10px #30ffba33;
        }
    }

    &:before {
        content: "";
        display: block;
        position: absolute;
        left: -50px;
        top: -50px;
        width: calc(100% + 25%);
        height: 100%;
        background-color: #e9e9ff;
        border-radius: 30px;
        z-index: -1;
    }

    .subtitle {
        display: block;
        font-size: 16px;
        margin-bottom: 5px;
        opacity: 0.5;
    }

    h2.password {
        position: relative;
        cursor: pointer;
        display: inline;
        font-family: "Inter", sans-serif;
        font-weight: 900;
        font-size: 24px;
        border-radius: 15px;
        outline: none;
        width: 100%;
        margin: 0;
        word-wrap: break-word;
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            left: 0;
            bottom: 0;
            background-color: black;
        }
        &:hover {
            color: gray;
            &::after {
                background-color: gray;
            }
        }
        &.copied::before {
            display: flex;
            align-items: center;
            justify-content: center;

            font-size: 0.5em;
            font-weight: 100;
            color: white;
            content: "copied";
            position: absolute;
            right: -120px;
            top: 20%;

            background-color: darkslategray;
            width: 50px;
            height: 80%;
            border-radius: 8px;
            opacity: 0;

            animation-name: copied;
            animation-duration: 2.2s;

            @keyframes copied {
                0% {
                    right: -120px;
                    opacity: 0;
                }
                40% {
                    right: -60px;
                    opacity: 1;
                }
                100% {
                    right: -60px;
                    opacity: 0;
                }
            }
        }
    }
    input[type="range"] {
        width: 100px;
    }
}

.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
}

.pwg-length {
    width: 80px;
    padding: 10px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

.styled-checkbox {
    position: absolute;
    opacity: 0;

    & + label {
        position: relative;
        cursor: pointer;
        padding: 0;
    }

    // Box.
    & + label:before {
        content: "";
        display: inline-block;
        vertical-align: text-top;
        width: 22px;
        height: 22px;
        background: white;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 8px;
    }

    // Box hover
    &:hover + label:before {
        background: #f9f9f9;
    }

    // Checkmark. Could be replaced with an image
    &:checked + label:after {
        content: "";
        position: absolute;
        left: 7px;
        top: 11px;
        background: white;
        width: 2px;
        height: 2px;
        box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white,
            4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
        transform: rotate(45deg);
    }
    // Box checked
    &:checked + label:before {
        background: #41dab9;
    }
    &:checked.disabled + label:before {
        background: #ade0d6;
    }
}

.noselect {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}

.pass-length {
    display: flex;
    & * {
        margin-left: 10px;
    }
}
